<template>
  <div class="wrapper">
    <v-head></v-head>

        <router-view></router-view>
 
    <!-- <v-sidebar v-on:childByValue="childByValue"></v-sidebar>
        <div class="content-box" :class="{'content-collapse':collapse,minPaddingLeft:minPaddingLeft,maxPaddingLeft:maxPaddingLeft}">
            <div class="content">
              <div class="wrap">
                <transition name="move" mode="out-in">
                    <keep-alive :include="tagsList">
                        <router-view></router-view>
                    </keep-alive>
                </transition>
              </div>
            </div>
        </div> -->
  </div>
</template>

<script>
import vHead from "./Header.vue";
import vSidebar from "./Sidebar.vue";
import vTags from "./Tags.vue";
import bus from "./bus";
export default {
  data() {
    return {
      tagsList: [],
      collapse: false,
      minPaddingLeft: false,
      maxPaddingLeft: true
    };
  },
  methods: {
    childByValue: function(childValue) {
      var windowWidth = window.innerWidth;
      if (windowWidth >= 1366) {
        if (childValue) {
          // childValue就是子组件传过来的值
          this.minPaddingLeft = childValue;
          this.maxPaddingLeft = !childValue;
        } else {
          this.minPaddingLeft = childValue;
          this.maxPaddingLeft = !childValue;
        }
      } else {
        if (childValue) {
          // childValue就是子组件传过来的值
          this.minPaddingLeft = !childValue;
        } else {
          console.log(childValue);
          this.minPaddingLeft = !childValue;
        }
      }
    }
  },
  components: {
    vHead,
    vSidebar,
    vTags
  },
  created() {
    var windowWidth = window.innerWidth;
    if (windowWidth >= 1366) {
      this.maxPaddingLeft = true;
    }
    bus.$on("collapse", msg => {
      this.collapse = msg;
    });

    // 只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
    bus.$on("tags", msg => {
      let arr = [];
      for (let i = 0, len = msg.length; i < len; i++) {
        msg[i].name && arr.push(msg[i].name);
      }
      this.tagsList = arr;
    });
  }
};
</script>

<style>
</style>



